$('.lang-select').click(function (e) {
    e.preventDefault();
    e.stopPropagation();
    $('.lang-select').removeClass('active');
    var $this = $(this);
    $this.addClass('active');
    var lang = $this.attr('data-lang');
    if (lang === 'lang-zh') {
        $('body').removeClass('lang-en').addClass('lang-zh');
        window.localStorage.setItem('lang', 'lang-zh');
    } else {
        $('body').removeClass('lang-zh').addClass('lang-en');
        window.localStorage.setItem('lang', 'lang-en');
    }

    $(document).trigger("switch-lang");

    if (browser.isMobile() === true) {
        let $navigation = $(".nav-mobile .navigation");
        $navigation.css({
            right: '-100%'
        });
    }
});
